import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/HomeView.vue'),
    meta: { title: 'H thng o to' }
  },
  {
    path: '/java-core',
    name: 'java-core',
    component: () => import('../views/JavaCoreView.vue'),
    meta: { title: 'o to Java Core' }
  },
  {
    path: '/spring-boot',
    name: 'spring-boot',
    component: () => import('../views/SpringBootView.vue'),
    meta: { title: 'o to Spring Boot' }
  },
  {
    path: '/vuejs',
    name: 'vuejs',
    component: () => import('../views/VueJSView.vue'),
    meta: { title: 'o to VueJS' }
  },
  {
    path: '/java-core/:topic',
    name: 'java-core-topic',
    component: () => import('../views/JavaCoreTopicView.vue'),
    meta: { title: 'Chi tit Java Core' }
  },
  {
    path: '/spring-boot/:topic',
    name: 'spring-boot-topic',
    component: () => import('../views/SpringBootTopicView.vue'),
    meta: { title: 'Chi tit Spring Boot' }
  },
  {
    path: '/vuejs/:topic',
    name: 'vuejs-topic',
    component: () => import('../views/VueJSTopicView.vue'),
    meta: { title: 'Chi tit VueJS' }
  }
]

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes
})

// Set page title
router.beforeEach((to, from, next) => {
  document.title = to.meta.title || 'H thng o to'
  next()
})

export default router
